@font-face {
  font-family: Gilroy;
  font-style: normal;
  font-weight: normal;
  src: local(Gilroy), url("Gilroy-Regular.53cdd398.woff2") format("woff2"), url("Gilroy-Regular.902236a4.woff") format("woff");
}

@font-face {
  font-family: Gilroy Semibold;
  font-style: normal;
  font-weight: normal;
  src: local(Gilroy Semibold), url("Gilroy-Semibold.eff0d7ae.woff2") format("woff2"), url("Gilroy-Semibold.83b64308.woff") format("woff");
}

html, body {
  height: 100%;
  color: #1f3f68;
  font-family: Gilroy, serif;
}

.wrapper {
  min-height: 100%;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

.main {
  flex: auto;
}

.container {
  max-width: 940px;
  margin: 0 auto;
  display: flex;
}

._color-white {
  color: #fff;
}

.button {
  height: 56px;
  width: 220px;
  color: #fff;
  letter-spacing: .5px;
  cursor: pointer;
  background: #5a98d0;
  border-radius: 10px;
  margin-bottom: 30px;
  padding-left: 25px;
  padding-right: 25px;
  font-family: inherit;
  font-size: 16px;
  transition: letter-spacing .3s, opacity .5s;
}

@media screen and (max-width: 375px) {
  .button {
    width: 176px;
    height: 46px;
  }
}

.button:hover {
  opacity: .8;
  letter-spacing: .15rem;
}

.header {
  width: 100%;
  background-color: #fff;
}

.header-container {
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 48px 20px;
  display: flex;
}

.header-container .logo {
  align-items: center;
  display: flex;
  position: relative;
}

.header-container .logo__circle-blue {
  width: 32px;
  height: 32px;
  z-index: 11;
  background-color: #316099;
  border: 2px solid #316099;
  border-radius: 50%;
  animation-name: orbit;
  animation-duration: 5s;
  position: relative;
}

@keyframes orbit {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(60px);
  }
}

.header-container .logo__circle-white {
  width: 32px;
  height: 32px;
  z-index: 12;
  background-color: #fff;
  border: 2px solid #316099;
  border-radius: 50%;
  transform: translateX(-50%);
}

.header-container .logo__letter {
  color: #316099;
  z-index: 13;
  font-family: Gilroy Semibold;
  font-size: 2rem;
  position: absolute;
  transform: translateX(26px)translateY(-1px);
}

.header-container .logo:hover {
  cursor: pointer;
  opacity: .8;
}

.header-container .header-menu {
  display: flex;
}

.header-container .header-menu .menu__body {
  width: 100%;
  background-color: #fff;
  display: none;
  overflow: auto;
}

.header-container .header-menu .menu__body .menu__list {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 40px;
  display: flex;
}

.header-container .header-menu .menu__body .menu__list .menu__item {
  letter-spacing: .05rem;
  font-family: Gilroy Semibold, serif;
  font-size: 2rem;
}

.header-container .header-menu .menu__body .menu__list .menu__item .menu__link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1024px) {
  .header-container .header-menu .menu__body .menu__list .menu__item {
    letter-spacing: normal;
  }
}

@media screen and (max-width: 819px) {
  .header-container .header-menu .menu__body .menu__list .menu__item {
    font-size: 2.4rem;
  }
}

@media screen and (max-width: 1024px) {
  .header-container .header-menu .menu__body .menu__list {
    gap: 24px;
  }
}

@media screen and (max-width: 819px) {
  .header-container .header-menu .menu__body .menu__list {
    flex-direction: column;
    justify-content: flex-start;
    display: flex;
  }

  .header-container .header-menu .menu__body {
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: #fff;
    justify-content: center;
    padding: 90px 20px 30px;
    position: fixed;
    top: 0;
    left: 0;
    overflow: auto;
  }
}

.header-container .close-icon-menu, .header-container .icon-menu {
  z-index: 11;
  height: 16px;
  width: 24px;
  flex-direction: column;
  flex: 0 0 24px;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.header-container .close-icon-menu span, .header-container .icon-menu span {
  height: 2px;
  background-color: #316099;
  border: 0;
}

.header-container .icon-menu:before, .header-container .icon-menu:after {
  content: "";
  height: 2px;
  background-color: #316099;
}

.header-container .close-icon-menu {
  display: none;
}

.header-container .close-icon-menu:before, .header-container .close-icon-menu:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #eb5757;
  position: absolute;
  top: 50%;
  left: 0;
}

.header-container .close-icon-menu:before {
  transform: rotate(45deg);
}

.header-container .close-icon-menu:after {
  transform: rotate(-45deg);
}

@media screen and (max-width: 1024px) {
  .header-container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.header:target .menu__body {
  display: flex;
}

@media screen and (max-width: 819px) {
  .header:target .menu__body:before {
    content: "";
    height: 90px;
    width: 100%;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
  }
}

.header:target .icon-menu {
  display: none;
}

.header:target .close-icon-menu {
  display: flex;
}

